import { css } from 'lit';

export const styles = css`[part~=label]{color:var(--label-color)}:host([disabled]) [part~=label],:host(:disabled) [part~=label]{color:var(--disabled-label-color)}:host(:not([disabled])[invalid]) [part~=label],:host(:not(:disabled)[invalid]) [part~=label]{color:var(--error-color)}`;
